import { Flex, Heading, keyframes, Spacer, Stack } from "@chakra-ui/react";
import React, { lazy, FC, MouseEventHandler, ReactNode } from "react";

interface DashboardCardItemProps {
  icon: ReactNode;
  cardName: string;
  index?: number;
  onClick?: MouseEventHandler<HTMLDivElement> | undefined;
}

const slide = keyframes`
  0%,100% {
    left: 0%;
  }
  50% {
    left: 100%;
  }
`;

const DashboardCardItem: FC<DashboardCardItemProps> = ({
  icon,
  cardName,
  onClick,
  index,
}): JSX.Element => {
  const { root, text } = useStyles();

  return (
    <Stack
      className={"dash-cards"}
      {...root}
      as="button"
      bg="dark"
      color="white"
      _hover={{
        bg: "dark",
        color: index === 1 ? "#FF00FF" : "#00FFFF",
        // transform: "scale(1.02)",
        // transition: "all .2s linear",
      }}
      pos="relative"
      overflow={"hidden"}
      onClick={onClick}
    >
      {icon}
      <Spacer />
      <Heading _hover={{ color: "white" }} {...text}>
        {cardName}
      </Heading>
      {/* <Flex className="dash-cards" /> */}
    </Stack>
  );
};

export default DashboardCardItem;

const useStyles = () => {
  return {
    root: {
      borderRadius: {
        base: "10px",
        md: "14px",
      },
      bg: "grey",
      p: ["10px", "15px", "18px", "20px", "25px"],
      h: "140px",
    },
    text: {
      fontSize: ["12px", "14px", "16px", "16px", "18px"],
      color: "white",
    },
  };
};
